@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
*{margin:0;padding:0}body{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;background-color:#383838;font-family:'Roboto'}

.userName{font-weight:700;margin-bottom:10px}.userNamePrefix{font-weight:300}

.userCard{background-color:whitesmoke;padding:10px;border-radius:5px}.todoTitle{font-size:20px;margin-bottom:10px}.todoStatus{font-weight:300}

.todoList{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;grid-gap:20px;gap:20px;list-style:none;padding:40px 0 40px}

